import React, { useState } from "react";
import { Space, Table as AndTable, Tag } from "antd";
import { useGetDataQuery } from "../services/data";
function Table(props) {
    const [params, setParams] = useState(props);
    const { data, isLoading } = useGetDataQuery({ ...params });
    return (
        data?.at(0) && (
            <AndTable
                columns={Object.keys(data?.at(0)).map((item) => ({
                    title: item,
                    dataIndex: item,
                    key: item,
                    render: (i) => i.toString(),
                }))}
                dataSource={data}
                size="small"
                scroll={{
                    x: 1300,
                }}
                loading={isLoading}
            />
        )
    );
}

export default Table;
