import { Breadcrumb, Layout, Menu, Spin, theme } from 'antd';
import { useState } from 'react';
import Footer from './Layout/Footer';
import {
  ProfileOutlined, BuildOutlined, FileImageOutlined
} from '@ant-design/icons';
import ContentManage from './ContentManager';
import { useDataQuery } from './services/data';
const { Header, Content, Sider } = Layout;
const App = () => {
  const [activeMenu, setActiveMenu] = useState("content-manager")
  const [collapsed, setCollapsed] = useState(false);
  const {
    token: { colorBgContainer },
  } = theme.useToken();
  const items = [
    {
      label: 'Content Manager',
      key: 'content-manager',
      icon: <ProfileOutlined />
    },
    {
      label: 'Modules',
      key: 'app',
      disabled: true,
    },
    {
      label: 'Media',
      key: 'media',
      icon: <FileImageOutlined />
    },
    {
      label: 'Content Builder',
      key: 'content-builder',
      icon: <BuildOutlined />
    },
  ];
  const { isLoading, data } = useDataQuery()
  return (
    <Layout
      style={{
        minHeight: '100vh',
      }}
    >
      <Sider collapsible collapsed={collapsed} onCollapse={(value) => setCollapsed(value)}>
        <div
          style={{
            height: 32,
            margin: 16,
            background: 'rgba(255, 255, 255, 0.2)',
          }}
        />
        <Menu theme="dark" defaultSelectedKeys={[activeMenu]} mode="inline" items={items}
          onSelect={({ key }) => {
            setActiveMenu(key);
          }} />
      </Sider>
      {!isLoading &&
        data ?
        <Layout className="site-layout">
          <Header
            style={{
              padding: 0,
              background: colorBgContainer,
            }}
          />
          {activeMenu === "content-manager" && <ContentManage />
          }
          <Footer />
        </Layout> : <Spin size='large' />}
    </Layout>
  );
};
export default App;