import { createApi, fetchBaseQuery } from "@reduxjs/toolkit/query/react";
import format from "../utils/format";
export const data = createApi({
    reducerPath: "data",
    baseQuery: fetchBaseQuery({
        baseUrl: "/",
        mode: "no-cors",
        headers: {
            "Content-Type": "application/json", // Add this line to set the content type header
        },
    }),
    tagTypes: ["data"],
    endpoints: (builder) => ({
        data: builder.query({
            query: () => {
                return {
                    url: `/ui-data-init.json`,
                    method: "GET",


                };
            },
            providesTags: ["data"],
            transformResponse: (res) => {
                return format(res);
            },
        }),
        getData: builder.query({
            query: ({ path }) => {
                return {
                    url: path,
                    method: "GET",
                };
            },
            providesTags: ["getall"],
        }),
    }),
});

export const { useDataQuery, useGetDataQuery } = data;
