function format(data) {
    const paths = data.paths;
    const schemas = data.components.schemas;
    const rousts = Object.entries(paths)
        .map((i) =>
            Object.entries(i[1]).map((k) => ({
                path: i[0],
                method: k[0],
                ...k[1],
                schema:
                    schemas[
                    k[1]?.requestBody?.content["application/json"]?.schema?.$ref
                        .split("/")
                        .at(-1)
                    ],
                name: k[1]?.tags?.at(0).charAt(0).toUpperCase() + k[1]?.tags?.at(0).slice(1)

            }))
        )
        .flat();

    const collection = rousts.filter(item => item?.tags?.includes("collection"))
    const single = rousts.filter(item => item?.tags?.includes("single"))
    return { ...data.info, schemas, rousts, types: { collection, single } };
}

export default format;
