import React, { useEffect, useState } from "react";
import { Layout, Menu, theme } from "antd";
import format from "../utils/format";
import { useDataQuery } from "../services/data";
import Table from "./Table";

const { Content, Sider } = Layout;

function ContentManage() {
    const [activeTab, setActiveTab] = useState([{}]);
    const { data: swaggerData } = useDataQuery();
    useEffect(() => {
        setActiveTab(swaggerData?.types?.collection[0]);
    }, [swaggerData]);
    return (
        <>
            <Layout>
                <Sider theme="light">
                    {swaggerData?.types?.collection?.at(0) &&
                        <Menu
                            theme="light"
                            defaultSelectedKeys={[activeTab?.name]}
                            mode="inline"
                            items={swaggerData?.types?.collection.map((i) => ({
                                label: i?.name,
                                key: i?.operationId,
                            }))}
                            onSelect={({ key }) => {
                                setActiveTab(
                                    swaggerData?.types?.collection.find((i) => i.operationId == key)
                                );
                            }}
                        />
                    }
                </Sider>
                <Layout>
                    <Content style={{
                        padding: "5px"
                    }}>
                        {activeTab?.name &&
                            <Table {...activeTab} />}
                    </Content>
                </Layout>
            </Layout>
        </>
    );
}

export default ContentManage;
